.icon { 
  width: 40px; 
  height: 40px;
  fill: #000;
  stroke: #000;
  transition: inherit;
}
.scroll {  
  position: relative;
  &:before,
  &:after {
    content: '';
    display: block;
    width: calc(100% - 30px);
    height: 15px;
    left: 15px;
    z-index: 2;
    position: absolute;
  }
  &:before {
    top: 0;
    background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.49) 51%,rgba(255,255,255,0) 100%);
  }
  &:after {
    bottom: 0;
    background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,0.49) 49%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,1) 100%);
  }
}
.scroll-container {
  overflow: auto;
  .nicescroll-rails {
    width: 15px !important;
  }
  .nicescroll-cursors {
    width: 15px !important;
    padding: 15px 6px !important;
    position: relative;
    &:after {
      width: 3px;
      height: calc(100% - 30px);
      top: 15px;
      left: 6px;
      border-radius: 3px;
    }
  }
}
.scroll-wrapper {
  padding: 15px;
  flex: 1 1 100%;
}

//
.button {
  outline: none;
  box-shadow: none;
  border-radius: 0;
  cursor: pointer;
  transition: 0.15s;
  line-height: 1;
  padding: 0;
  border: 0;
  flex-shrink: 0;
  display: inline-block;
  text-decoration: none;
  color: #202020;
  &_style1 {
    border-radius: 4px;
    background-color: $main-color;
    padding: 14px 20px;
    color: #fff;
    font-family: 'Manrope';
    .icon {
      fill: #fff;
    }
    &:hover {
      background-color: $hover-color;
    }
  }
  &_style2 {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .icon {
      width: 20px;
      height: 20px;
      fill: #fff;
    }
    &:hover {
      opacity: 0.8;
    }
  }
  &_profile {
    background-color: #ED6842;
  }
  &_cart {
    position: relative;
    background-color: #1A2434;
    .icon {
      transform: translateX(-1px);
    }
    .cart-count {
      position: absolute;
      left: -10px;
      top: -5px;
      width: 22px;
      height: 22px;
      line-height: 22px;
      font-size: 10px;
      background-color: #E52329;
      color: #fff;
      border-radius: 100%;
      text-align: center;
    }
  }
  &_style3 {
    padding: 10px 20px;
    display: block;
    border-radius: 4px;
    line-height: 1.25;
    &:hover {
      background-color: #f3f3f3;
      color: $main-color !important;
    }
  }
  &_style4 {
    border-radius: 4px;
    padding: 13px 19px;
    color: #000;
    font-family: 'Manrope';
    border: 1px solid $border-color;
    .icon {
      fill: #757575;
    }
    &:hover {
      border-color: #808080;
    }
  }
}
.active {
  .toggle-item {
    span {
      background-color: transparent;
      &:before {
        transform: rotate(-45deg);
        top: 0;
      }
      &:after {
        transform: rotate(45deg);
        top: 0;
      }
    }
  }
}
.input {
  line-height: 1;
  border: 0;
  box-shadow: none;
  outline: none;
  font-family: 'Roboto';
  appearance: none;
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  &::placeholder {
    color: #C4C4C4;
  }
  &:focus {
    &::placeholder {
      color: transparent;
    }
  }
  &_style1 {
    border: 1px solid $border-color;
    border-radius: 6px;
    padding: 8px;
    text-align: center;
    display: block;
    font-size: 16px;
    width: 100%;
    &:focus {
      border-color: #979797;
    }
  }
}
.dropdown-item {
  position: relative;
  &__button {
    .icon {
      pointer-events: none;
    }
  }
  &.visible {
    z-index: 150;
    .dropdown-item__container {
      visibility: visible;
      opacity: 1;
      transform: translateY(0);
    }
  }
  &__container {
    position: absolute;
    top: calc(100% + 5px);
    min-width: 180px;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    visibility: hidden;
    opacity: 0;
    transform: translateY(5px);
    z-index: 100;
    overflow: hidden;
    ul  {
      list-style: none;
      padding: 0;
      margin: 0;
    }
    a {
      padding: 6px 10px;
    }
    .scroll-container {
      max-height: 205px;
    }
  }
}

//checkboxes
.checkbox {
  position: relative;
  padding: 5px 5px 5px 30px;
  display: block;
  line-height: 16px;
  cursor: pointer;
  transition: 0.2s;
  &:before {
    content: '';
    display: block;
    width: 14px;
    height: 14px;
    border-radius: 2px;
    border: 1px solid #757575;
    background-color: #fff;
    position: absolute;
    left: 5px;
    top: 5px;
    transition: 0.2s;
  }
  &:after {
    content: '';
    display: block;
    width: 10px;
    height: 10px;
    position: absolute;
    left: 8px;
    top: 8px;
    background-image: url('../img/icons/check.svg');
    background-size: 100% 100%;
    opacity: 0;
    transition: 0.2s;
  }
  input {
    display: none;
  }
  &.checked {
    &:before {
      background-color: #ED6842;
      border-color: #ED6842;
    }
    &:after {
      opacity: 1;
    }
  }
}