@mixin font-face($font-family, $file-path, $weight: normal, $style: normal, $asset-pipeline: false ) {
	@font-face {
		font-family: $font-family;
		font-weight: $weight;
		font-style: $style;
		src: url('#{$file-path}.woff2') format('woff2'),
				url('#{$file-path}.woff') format("woff");
		
	}
}

@include font-face("Manrope", "../fonts/manrope-regular");
@include font-face("Manrope", "../fonts/manrope-medium", 500);
@include font-face("Manrope", "../../fonts/manrope-bold", 700);


